@import "colors";

nav {
  display: flex;
  justify-content: space-between;
  background-color: $secondary;
  padding: 0 2rem;
  > h2 {
    display: flex;
    align-items: center;
    padding: 4px;
    img{
      width: 40px;
      border-radius: 50%;
    }
  }
  > div {
    display: flex;
    align-items: center;
    > a {
      color: $primary;
      font-size: 1.2rem;
      margin: 0 1rem;
      transition: all 0.3s;
      &:hover {
        opacity: 0.5;
      }

      > p {
        position: absolute;
        background-color: $tertiary;
        border-radius: 50%;
        width: 15px;
        height: 15px;
        font: 900 0.7rem "Roboto";
        display: flex;
        align-items: center;
        justify-content: center;
        transform: translate(80%, -80%);
      }
    }
  }
}


